/* ProjectDetailPage.module.css */

.projectDetailContainer {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.projectDetail {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.projectDetail h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #333;
}

.projectDetail .markdown-container {
  font-size: 18px;
  color: #666;
  line-height: 1.6;
}

