/* ContactPage.module.css */

.contactContainer {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.contactForm {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.contactForm h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #333;
}

.contactForm label {
  display: block;
  font-size: 18px;
  margin-bottom: 10px;
  color: #333;
}

.contactForm input[type="text"],
.contactForm textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.contactForm textarea {
  height: 200px;
}

.contactForm button {
  background-color: #007BFF;
  color: #fff;
  font-size: 18px;
  padding: 10px 20px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.contactForm button:hover {
  background-color: #0056b3;
}

/* 添加更多自定义样式根据需要 */