/* ContactInfo.module.css */

.contactInfoContainer {
  width: 800px;
  background-color: #f7f7f7;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 0 auto;
  margin-bottom: 20px;
 
}

.contactDetails p {
  font-size: 18px;
  margin: 10px 0;
  color: #333;
}

.contactDetails strong {
  font-weight: bold;
  color: #007BFF;
}

.socialIcons {
  text-align: center;
  margin-top: 20px;
}

.socialIcons a {
  font-size: 30px;
  margin: 0 10px;
  color: #007BFF;
  transition: color 0.2s;
}

.socialIcons a:hover {
  color: #0056b3;
}

