/* BlogsPage.module.css */

.blogsContainer {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.blogItem {
  background-color: #f7f7f7;
  border: 1px solid #ddd;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
  transition: transform 0.2s;
  list-style: none;
}

.blogItem:hover {
  transform: scale(1.02);
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
}

.blogItem h3 {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

.blogItem p {
  font-size: 18px;
  color: #666;
}

.blogItem a {
  text-decoration: none;
  color: #007BFF;
  font-weight: bold;
}

.blogItem a:hover {
color: #007BFF;
}

