/* ContactForm.module.css */

.contactFormContainer {
  background-color: #f7f7f7;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin-bottom: 20px;
  max-width: 800px;
  /* 控制表单宽度 */
  margin: 0 auto;
  /* 居中显示 */
}

.contactFormContainer h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

.contactFormContainer label {
  display: block;
  font-size: 18px;
  margin-bottom: 10px;
  color: #333;
}

.contactFormContainer input[type="text"],
.contactFormContainer input[type="email"],
.contactFormContainer textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin-bottom: 10px;
}

.contactFormContainer textarea {
  height: 150px;
}

.contactFormContainer button {
  background-color: #007BFF;
  color: #fff;
  font-size: 18px;
  padding: 10px 20px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.contactFormContainer button:hover {
  background-color: #0056b3;
}

/* 添加更多自定义样式根据需要 */