/* Navbar.module.css */

.navbar {
  background-color: #333;
  color: white;
  padding: 16px;
}

.navbar ul {
  list-style: none;
  padding: 0;
  display: flex;
}

.navbar li {
  margin-right: 20px;
}

/* 更新链接样式，包括字体大小和悬停效果 */
.navbar a {
  padding: 15px;
  text-decoration: none;
  color: white;
  transition: background-color 0.3s, font-size 0.3s;
  /* 添加过渡效果 */
  font-size: 18px;
  /* 默认字体大小 */
}

/* 添加悬停样式，包括字体大小的变化 */
.navbar a:hover {
  background-color: #555;
  text-decoration: underline;
  font-size: 20px;
  /* 悬停时的字体大小 */
}